.navBar {
    background-color: rgb(250, 250, 250);
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25); 
}

.navTitle {
    font-family: 'Montserrat', sans-serif;
    padding: 2px;    
    font-size: 45px;
    font-weight: 500;
    margin-left: 10px;
    color: orange;
    cursor: pointer;
}

