.navSignOut {
    background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%), #D6D5D5;
    width: 300px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    font-size: 12px;
    margin-right: 50px;
    padding: 10px;
    margin-top: 19px;
    border-radius: 20px;   
    color: black;
    /* box-shadow: 0px 2px 1px grey;  */
}

.navSignOut:hover {
    color: rgb(174, 0, 255);
}

.navSignOut:active {
    transform: translateY(1px);
}